/* Based on Sublime Text's Monokai theme */
.CodeMirror {
  overflow: initial !important;
}
.CodeMirror pre {
  font-family: inherit !important;
  font-size: inherit !important;
  margin-bottom: 0;
  overflow: hidden;
}
/* .cm-s-monokai.CodeMirror { background: #eff1f3; color: #3f51b5; } */
.cm-s-monokai.CodeMirror {
  background: transparent;
  border-radius: 0px;
}
.cm-s-monokai div.CodeMirror-selected {
  background: #d4dde6;
}
.cm-s-monokai .CodeMirror-line::selection,
.cm-s-monokai .CodeMirror-line > span::selection,
.cm-s-monokai .CodeMirror-line > span > span::selection {
  background: #d4dde6;
}
.cm-s-monokai .CodeMirror-line::-moz-selection,
.cm-s-monokai .CodeMirror-line > span::-moz-selection,
.cm-s-monokai .CodeMirror-line > span > span::-moz-selection {
  background: #d4dde6;
}
.cm-s-monokai .CodeMirror-guttermarker {
  color: white;
}
.cm-s-monokai .CodeMirror-guttermarker-subtle {
  color: #d0d0d0;
}
.cm-s-monokai .CodeMirror-lines {
  padding: 6px 5px;
}
.cm-s-monokai .CodeMirror-linenumber {
  color: #676767;
  padding-right: 10px;
}
.cm-s-monokai .CodeMirror-cursor {
  border-left: 2px solid #14140a;
}
.cm-s-monokai span.cm-comment {
  color: #75715e;
}
.cm-s-monokai span.cm-atom {
  color: #ae81ff;
}
.cm-s-monokai span.cm-number {
  color: #ae81ff;
}
.cm-s-monokai span.cm-comment.cm-attribute {
  color: #97b757;
}
.cm-s-monokai span.cm-comment.cm-def {
  color: #bc9262;
}
/* .cm-s-monokai span.cm-comment.cm-tag { color: #bc6283; } */
.cm-s-monokai span.cm-comment.cm-type {
  color: #5998a6;
}
.cm-s-monokai span.cm-property,
.cm-s-monokai span.cm-attribute {
  color: #009688;
}
.cm-s-monokai span.cm-keyword {
  color: #f92672;
}
.cm-s-monokai span.cm-builtin {
  color: #66d9ef;
}
.cm-s-monokai span.cm-string {
  color: #f44336;
}
.cm-s-monokai span.cm-variable {
  color: #3f51b5;
}
.cm-s-monokai span.cm-variable-2 {
  color: #2196f3;
}
.cm-s-monokai span.cm-variable-3,
.cm-s-monokai span.cm-type {
  color: #66d9ef;
}
.cm-s-monokai span.cm-def {
  color: #fd971f;
}
.cm-s-monokai span.cm-keyword + .cm-def {
  color: #008879;
}
.cm-s-monokai span.cm-bracket {
  color: #3f51b5;
}
.cm-s-monokai span.cm-tag {
  color: #3f51b5;
}
.cm-s-monokai span.cm-header {
  color: #ae81ff;
}
.cm-s-monokai span.cm-link {
  color: #ae81ff;
}
.cm-s-monokai span.cm-error {
  background: #f92672;
  color: #f8f8f0;
}
.cm-s-monokai .CodeMirror-activeline-background {
  background: #373831;
}
.cm-s-monokai .CodeMirror-matchingbracket {
  color: #000000 !important;
  background-color: #c1c1c1;
  border-radius: 2px;
}
